import React, { Component} from 'react'

import { Row, Col, Card, Table } from 'react-bootstrap';

import Aux from '../../hoc/_Aux';

import { cases } from '../../case_data';
import { Redirect } from 'react-router-dom';


const monthMap = [null, 'Jan', 'Feb', "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];

export function CaseBrowseTable(props) {

    const casePreviews = cases.map((caseObj) => {

        const compensation_ask = `${caseObj.compensation_ask}`;
        let format_compensation_ask = "";
        let i = 0;

        do {
            format_compensation_ask += compensation_ask[i];
            ++i;
        } while (i < compensation_ask.length && (compensation_ask.length - i)%3 !== 0);

        while (i < compensation_ask.length) {
            format_compensation_ask += `,${compensation_ask[i]}${compensation_ask[i+1]}${compensation_ask[i+2]}`;
            i += 3;
        }
        

        
        return (
            <tr key={caseObj.id} onClick={e => props.redirect(`/case/${caseObj.id}`)} onMouseEnter={e => e.target.parentElement.style.cursor = "pointer"}>
                <td><i className="fa fa-balance-scale f-30" /></td>
                <td>
                    <h6 className="mb-1">{caseObj.case_name}</h6>
                    <p className="m-0">{caseObj.issue}</p>
                </td>
                <td>
                    <h6 className="text-muted"><i className="fa fa-circle text-c-green f-10 m-r-15"/>{`${monthMap[caseObj.incident_date.M]} ${caseObj.incident_date.D}`}</h6>
                </td>
                <td>
                    {`$${format_compensation_ask}`}
                </td>
            </tr>
        );
    });

    return (
        <tbody>
            { casePreviews }
        </tbody>
    );
}

export default class CaseExplorer extends Component {
    constructor(props) {
        super(props);

        this.state = {
            redirect: false,
            redirect_to: undefined,
        }

        this.setRedirect = this.setRedirect.bind(this);
    }

    setRedirect(route) {
        this.setState({ redirect: true, redirect_to: route });
    }


    render() {

        if (this.state.redirect) {
            return <Redirect push to={this.state.redirect_to} />
        }

        return (
            <Aux>
                <Row>
                    {/* Case Feed */}
                    <Col lg={9} md={9} sm={12} xs={12}>
                        <Card className="Recent-Users">
                            <Card.Header>
                                <Card.Title as="h5">Cases Suggested for You</Card.Title>
                            </Card.Header>
                            <Card.Body className="px-0 py-2">
                                <Table responsive hover>
                                    <CaseBrowseTable redirect={this.setRedirect} />
                                </Table>
                            </Card.Body>
                        </Card>
                    </Col>

                    {/* Trending Cases */}
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">Trending Cases</Card.Title>
                            </Card.Header>
                            <Card.Body>
                                <ul>
                                    <li>Marbury v. Madison</li>
                                    <li>Angular v. React</li>
                                    <li>Coke v. Pepsi</li>
                                    <li>Unix v. MSDOS</li>
                                    <li>Functional v. Object Oriented</li>
                                </ul>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
        )
    }
}
